      //     router.push({ path: '/dashboard' })
      //   }else{
      //     router.push({ path: '/data' })
      //   }
      // }

      if(to.meta.rule != 'PUBLIC') {
        if (!(auth.isAuthenticated() )) {
          //console.log(auth.isAuthenticated());
          router.push({ path: '/pages/login', query: { to: to.path } })
        }
      }
      // else{
      return next()
