// import auth0 from 'auth0-js'
import EventEmitter from 'events'
// import authConfig from '@/../auth_config.json'

import store from "@/store/store.js"
import axios from "../axios"

// 'loggedIn' is used in other parts of application. So, Don't forget to change there also
const localStorageKey = 'token';

const tokenExpiryKey = 'tokenExpiry';
const loginEvent = 'loginEvent';


class AuthService extends EventEmitter {
    idToken = null;
    profile = null;
    tokenExpiry = null;

    localLogin(authResult,acl) {
        
        acl.change(authResult.userRole);
        // console.log(authResult.token)
        axios.defaults.headers.common['x-access-token'] =  authResult.token
        

        store.commit("UPDATE_USER_INFO", {
            displayName: authResult.displayName,
            uid:authResult.uid,
            userRole:authResult.userRole,
            brand:authResult.brandname,
            app:authResult.app,
            loggedIn:true,
            entitas:authResult.entitas,
            token:authResult.token
        })
    }


    logOut() {
        localStorage.removeItem(localStorageKey);
        localStorage.removeItem(tokenExpiryKey);
        localStorage.removeItem('userInfo');

        axios.defaults.headers.common['token'] =  ''

        this.idToken = null;
        this.tokenExpiry = null;
        this.profile = null;

        this.emit(loginEvent, { loggedIn: false });
    }

    getRole(){
        return store.state.AppActiveUser.userRole
    }

    getBrand(){
        return store.state.AppActiveUser.brandname
    }

    isAuthenticated() {
        return (
            store.state.AppActiveUser.loggedIn === true
        );
    }
}

export default new AuthService();
