// axios
import Vue from 'vue'
import axios from 'axios'
import store from "@/store/store.js"
import router from './router'
import auth from "@/auth/authService"

const baseURL = process.env.VUE_APP_URL

const token = store.state.AppActiveUser.token;

const axiosInstance = axios.create({
  baseURL:baseURL,
});

// axiosInstance.interceptors.request.use(function(config) {
  //   console.log('Start with Ajax call');
  //   // Do something before request is sent
  //   // this.$vs.loading()
  //   // Vuesax.loading();
  //   return config;
  //   }, function(error) {
    //   // Do something with request error
    //   console.log('Error with Ajax call');
    //   // this.$vs.loading.close()
    //   return Promise.reject(error);
    // });
    
axiosInstance.interceptors.response.use(function(response) {
  // if(response.data.status == '998'){
  if(response.data.message == "Failed to authenticate token."){
    auth.logOut();
    Vue.swal.fire({
      title: 'Error',
      text: "Login anda telah habis !",
      icon: 'warning',
    })
    localStorage.removeItem("userInfo");
    return router.push('/pages/login').catch(() => {})
  }
  return response;
}, function(error) {
  // Do something with response error
  //console.log('Error fetching the data');
  // this.$vs.loading.close()
  return Promise.reject(error);
});
axiosInstance.defaults.headers.common['x-access-token'] = token
    
export default axiosInstance;