import Vue from "vue"
import { AclInstaller, AclCreate, AclRule } from "vue-acl"
import router from "@/router"

Vue.use(AclInstaller)

let initialRole = "public"

let userInfo = JSON.parse(localStorage.getItem("userInfo"))
if(userInfo && userInfo.userRole) {
  if(userInfo.brand != "") {
    initialRole = userInfo.userRole + '_' + userInfo.brand
  } else {
    initialRole = userInfo.userRole
  }
}
// initialRole = userInfo.userRole

export default new AclCreate({
  initial: initialRole,
  notfound: "/pages/login",
  router,
  acceptLocalRules: true,
  globalRules: {
    ADMIN: new AclRule("ADMIN").generate(),
    WILAYAH: new AclRule("WILAYAH").or('ADMIN').generate(),
    BOD : new AclRule("BOD").or('KAREG').or('AM').or('KACAB').or('KAWIL').generate(),
    AGEN_ADMIN: new AclRule("AGEN_ADMIN").generate(),
    MARKETING: new AclRule("AGEN_ADMIN").or("MARKETING").generate(),
    GUDANG: new AclRule("GUDANG").generate(),
    // KAWIL_ROSCIK: new AclRule('KAWIL_ROSCIK').generate(),
    PUBLIC: new AclRule("PUBLIC").or("BOD").or("WILAYAH").or("ADMIN").generate(),
  }
})
